import { NativeEventEmitter, NativeModules } from 'react-native';

export interface TracerouteResult {
  stdout: string;
  stderr: string;
  exitcode?: number;
}

const TracerouteEventEmitter = new NativeEventEmitter(NativeModules.TracerouteModule);

export function Traceroute(
  cliArgs: string[],
  onUpdate: (result: TracerouteResult) => void
) {
  let id: string | undefined;
  const onEvent = (evt: TracerouteResult) => {
    onUpdate(evt);
    if (evt.exitcode !== undefined && id) {
      TracerouteEventEmitter.removeListener(id, onEvent);
    }
  };
  NativeModules.Traceroute.doTraceroute(cliArgs).then((eventId: string) => {
    id = eventId;
    TracerouteEventEmitter.addListener(id, onEvent);
  });
}

export default Traceroute;
